*{box-sizing:border-box;padding:0;margin:0}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.game{display:flex;justify-content:center;align-items:center;flex-direction:column;min-height:100vh;width:100%;background-image:url(/RoadGame/static/media/bgImage.606602c8.jpg);background-position:center;background-size:cover;position:relative;overflow:hidden}.game__top-box{display:flex;justify-content:space-between;align-items:center;width:300px;height:30px;border-top-left-radius:10px;border-top-right-radius:10px;background-color:#3a0174;border:4px solid #47008f;border-bottom:none;padding:.8em}.game__board-layer{display:flex;justify-content:space-evenly;align-items:center;flex-direction:column;min-height:100vh;width:100%;padding:1.5em .5em;background-color:rgba(0,0,0,0.8)}.game__board{position:relative;display:grid;grid-template-rows:repeat(10, 1fr);grid-template-columns:repeat(10, 1fr);width:300px;height:300px;border:8px solid #47008f;border-bottom-left-radius:10px;border-bottom-right-radius:10px;background-color:rgba(59,1,116,0.938);margin:0px auto 10px}.game__board-wrapper{display:flex;justify-content:center;align-items:center;flex-direction:column}.game__button{height:50px;width:250px;border:none;font-size:2em;font-weight:bold;color:#fffb1e;background-color:#3a0174;border:4px solid #47008f;box-shadow:-.1em .1em .1em .1em black;border-radius:10px;outline:none;-webkit-transition:.4s;transition:.4s;margin:5px;cursor:pointer}.game__button:first-child{-webkit-animation:startAnimation .3s .6s ease-in both;animation:startAnimation .3s .6s ease-in both}.game__button:nth-child(2){-webkit-animation:startAnimation .3s .9s ease-in both;animation:startAnimation .3s .9s ease-in both}.game__button:nth-child(3){-webkit-animation:startAnimation .3s 1.2s ease-in both;animation:startAnimation .3s 1.2s ease-in both}.game__button:hover{background-color:#190031;border:4px solid #480091;color:#5d5f40}.game__button--disabled{background-color:#190031;border:4px solid #480091;color:#5d5f40}.game__parameter,.game__information{font-size:1em;font-weight:bold;color:#fffb1e}.game__parameter--counter,.game__information--counter{font-size:1em}.game__information--none{display:none}.game__information--correct{color:#00da00}.game__information--wrong{color:#d85600}.game__information--miss{color:red}.game__start-layer{display:flex;justify-content:space-evenly;align-items:center;flex-direction:column;min-height:100vh;width:100%;padding:1.5em .5em;background-color:rgba(0,0,0,0.7)}.game__button-box{display:flex;flex-direction:column}.game__title{font-size:3em;font-weight:bold;text-shadow:-.05em .05em .03em black;color:#fffb1e;-webkit-animation:startAnimation .3s ease-in both;animation:startAnimation .3s ease-in both}.game__subtitle{font-size:1.3em;font-weight:bold;text-shadow:-.05em .05em .03em black;color:#fffb1e;-webkit-animation:startAnimation .3s .3s ease-in both;animation:startAnimation .3s .3s ease-in both;margin-bottom:.5em}.game__parameter--counter{-webkit-animation:increaseVisibility 1s cubic-bezier(0.47, 0, 0.745, 0.715) both;animation:increaseVisibility 1s cubic-bezier(0.47, 0, 0.745, 0.715) both}.board__gameOver-caption,.board__winGame-caption{position:absolute;display:flex;justify-content:center;align-items:center;width:100%;height:100%;z-index:999}.board__gameOver-caption::before,.board__winGame-caption::before{content:"Game Over";position:absolute;font-size:3em;color:#fffb1e;text-shadow:-.05em .05em 0.05em black;-webkit-animation:gameOverAnimation 4s cubic-bezier(0.77, 0, 0.175, 1) both;animation:gameOverAnimation 4s cubic-bezier(0.77, 0, 0.175, 1) both}.board__gameOver-caption--none,.board__winGame-caption--none{display:none}.board__winGame-caption::before{content:"You Win!";-webkit-animation:winGameAnimation 4s cubic-bezier(0.77, 0, 0.175, 1) both;animation:winGameAnimation 4s cubic-bezier(0.77, 0, 0.175, 1) both}.square{display:flex;justify-content:center;align-items:center;border:1px solid black;border-radius:5px;-webkit-transition:.1s linear;transition:.1s linear;color:#47008f;font-size:1.5em;font-weight:bold;box-shadow:inset 0 0 4px 1.5px #14000f;cursor:pointer}.square:hover{background-color:rgba(0,0,0,0.445);box-shadow:inset 0 0 4px 1.5px #000}.startSquare{-webkit-animation:increaseVisibility 0.5s cubic-bezier(0.47, 0, 0.745, 0.715) both;animation:increaseVisibility 0.5s cubic-bezier(0.47, 0, 0.745, 0.715) both;background-color:#f80;box-shadow:inset 0 0 4px 1.5px #000}.startSquare:hover{background-color:#b96403}.drawRoad{-webkit-animation:increaseVisibility 0.5s cubic-bezier(0.47, 0, 0.745, 0.715) both;animation:increaseVisibility 0.5s cubic-bezier(0.47, 0, 0.745, 0.715) both;background-color:#b9ec00;box-shadow:inset 0 0 4px 1.5px #000}.drawRoad:hover{background-color:#89af00}.hitSquare{-webkit-animation:increaseVisibility 0.5s cubic-bezier(0.47, 0, 0.745, 0.715) both;animation:increaseVisibility 0.5s cubic-bezier(0.47, 0, 0.745, 0.715) both;background-color:#00da00;box-shadow:inset 0 0 4px 1.5px #000}.hitSquare:hover{background-color:#007000}.missSquare{-webkit-animation:increaseVisibility 0.5s cubic-bezier(0.47, 0, 0.745, 0.715) both;animation:increaseVisibility 0.5s cubic-bezier(0.47, 0, 0.745, 0.715) both;position:relative;background-color:rgba(255,0,0,0.384)}.missSquare:hover{background-color:rgba(175,0,0,0.384);box-shadow:inset 0 0 4px 1.5px #1d0000}.missSquare::before{content:"\2716";display:flex;justify-content:center;align-items:center;position:absolute;width:100%;height:100%;color:red;font-size:1em;padding-bottom:5%;-webkit-transition:.1s;transition:.1s}.missSquare:hover::before{color:#810000}@-webkit-keyframes increaseVisibility{from{opacity:0;-webkit-transform:scale(0);transform:scale(0)}to{opacity:1;-webkit-transform:scale(1);transform:scale(1)}}@keyframes increaseVisibility{from{opacity:0;-webkit-transform:scale(0);transform:scale(0)}to{opacity:1;-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes gameOverAnimation{0%{-webkit-transform:scale(0) rotate(0deg);transform:scale(0) rotate(0deg)}40%{-webkit-transform:scale(1) rotate(360deg);transform:scale(1) rotate(360deg);color:red}60%{-webkit-transform:scale(1) rotate(360deg);transform:scale(1) rotate(360deg);color:red}to{-webkit-transform:scale(0) rotate(0deg);transform:scale(0) rotate(0deg)}}@keyframes gameOverAnimation{0%{-webkit-transform:scale(0) rotate(0deg);transform:scale(0) rotate(0deg)}40%{-webkit-transform:scale(1) rotate(360deg);transform:scale(1) rotate(360deg);color:red}60%{-webkit-transform:scale(1) rotate(360deg);transform:scale(1) rotate(360deg);color:red}to{-webkit-transform:scale(0) rotate(0deg);transform:scale(0) rotate(0deg)}}@-webkit-keyframes winGameAnimation{0%{-webkit-transform:scale(0) rotate(0deg);transform:scale(0) rotate(0deg)}40%{-webkit-transform:scale(1) rotate(360deg);transform:scale(1) rotate(360deg);color:#26e400}60%{-webkit-transform:scale(1) rotate(360deg);transform:scale(1) rotate(360deg);color:#26e400}to{-webkit-transform:scale(0) rotate(0deg);transform:scale(0) rotate(0deg)}}@keyframes winGameAnimation{0%{-webkit-transform:scale(0) rotate(0deg);transform:scale(0) rotate(0deg)}40%{-webkit-transform:scale(1) rotate(360deg);transform:scale(1) rotate(360deg);color:#26e400}60%{-webkit-transform:scale(1) rotate(360deg);transform:scale(1) rotate(360deg);color:#26e400}to{-webkit-transform:scale(0) rotate(0deg);transform:scale(0) rotate(0deg)}}@-webkit-keyframes startAnimation{from{opacity:0}to{opacity:1}}@keyframes startAnimation{from{opacity:0}to{opacity:1}}@media (min-width: 450px){.game__title{font-size:4em}.game__subtitle{font-size:1.6em}.game__board{width:350px;height:350px}.game__top-box{width:350px;height:35px}.game__parameter,.game__information{font-size:1.3em}.game__parameter--counter,.game__information--counter{font-size:1.7em}.missSquare::before{font-size:1.3em}}@media (min-width: 768px){.game__title{font-size:6em}.game__subtitle{font-size:2.2em}.game__board{width:450px;height:450px}.game__top-box{width:450px;height:45px}.game__parameter,.game__information{font-size:1.6em}.game__parameter--counter,.game__information--counter{font-size:2.2em}.game__button-box--board{flex-direction:row}.board__gameOver-caption::before,.board__winGame-caption::before{font-size:4em}.missSquare::before{font-size:1.6em}}@media (min-width: 1200px){.game__title{font-size:7em}.game__subtitle{font-size:2.7em}.game__board{width:500px;height:500px}.game__top-box{width:500px;height:50px}.game__parameter,.game__information{font-size:1.8em}.game__parameter--counter,.game__information--counter{font-size:2.5em}.game__button-box{flex-direction:row}.board__gameOver-caption::before,.board__winGame-caption::before{font-size:5em}}.App{text-align:center;margin:0 auto;background-color:#3b3e44}

